import { useTranslation } from 'react-i18next';
import { useData } from 'vike-react/useData';
import { useState } from 'react';
import { FaArrowLeft } from 'react-icons/fa6';
import type { PageData } from './+data';
import { Text } from '~/components/ui/text';
import { Metadata } from '~/components/layout/Metadata';
import { Box, Stack } from 'styled-system/jsx';

import { ChartViewer } from '~/components/ChartViewer';
import { RadioButtonGroup } from '~/components/ui/radio-button-group';
import { Link } from '~/components/ui/link';

export function Page() {
  const { t } = useTranslation();

  const [difficulty, setDifficulty] = useState<1 | 2 | 3 | 4>(4);
  const { chartInfo, chartData }: PageData = useData();

  const chart = chartData[difficulty];
  const title = chartInfo.title;

  const options = [
    { label: 'NORMAL', value: 1 },
    { label: 'HARD', value: 2 },
    { label: 'EXPERT', value: 3 },
    { label: 'MASTER', value: 4 }
  ];

  return (
    <>
      <Metadata title={title} helmet />
      <Stack alignItems="center" w="full" _print={{ display: 'none' }}>
        <Box>
          <Link href="/">
            <FaArrowLeft />
            {t('back_to_list')}
          </Link>
        </Box>
        <Text textAlign="center" fontSize="3xl" fontWeight="bold">
          {title}
        </Text>
        <Text textAlign="center">{chartInfo.description}</Text>
        <RadioButtonGroup.Root
          value={String(difficulty)}
          onValueChange={(v) => setDifficulty(Number(v.value) as 1)}
        >
          {options.map((option) => (
            <RadioButtonGroup.Item key={option.value} value={option.value + ''} px="0">
              <RadioButtonGroup.ItemControl />
              <RadioButtonGroup.ItemText px={2}>{option.label}</RadioButtonGroup.ItemText>
              <RadioButtonGroup.ItemHiddenInput />
            </RadioButtonGroup.Item>
          ))}
        </RadioButtonGroup.Root>
        <Box position="relative" w="full" minH="screen" overflowX="auto">
          <Box position="absolute" h="full">
            <ChartViewer chart={chart} />
          </Box>
        </Box>
      </Stack>
    </>
  );
}
