import { Application, Container, Graphics, Text, TextStyle } from 'pixi.js';
import type { Flags } from './parseFlags';
import { parseFlags } from './parseFlags';
import type { ChartData, Note, PreProcessedChartFileSoA } from '~/types';
const NOTE_THICKNESS = 8;
const TRACE_NOTE_THICKNESS = 4;
const NOTE_CORNER_RADIUS = 4;
const GRID_LINE_THICKNESS = 4;
const COLUMN_SPACING = 40;
const BEATS_PER_COLUMN = 32;
const LANE_FLAG_MAX_VALUE = 60;

const CHART_PADDING = 40;
const FLOAT_COMPARISON_EPSILON = 1e-3;
const COMBO_COUNT_PADDING = 10;
const FLICK_ARROW_SIZE = 10;
const FLICK_ARROW_COLOR = 0xffffff;
const HOLD_END_COLUMN_ADJUST = 1e-6;

const NOTE_COLOR_SINGLE = 0xffc0cb;
const NOTE_COLOR_TRACE = 0xffff00;
const NOTE_COLOR_HOLD = 0x6495ed;
const HOLD_SEGMENT_ALPHA = 0.5;
const NOTE_COLOR_FLICK = 0x008000;
const CHART_AREA_BACKGROUND_COLOR = 0xf5f5f5; // Very light gray
const NOTE_COLOR_DEFAULT = 0xffffff;

type HoldLink = { type: 'beat'; beat: number } | { type: 'uid'; uid: number };
type ParsedNote = {
  uid: number;
  just: number;
  holds: HoldLink[];
  flags: Flags;
};

interface HoldSegmentDrawConfig {
  segmentInitialStartBeat: number;
  segmentFinalEndBeat: number;
  segmentInitialStartLaneL: number;
  segmentInitialStartLaneR: number;
  segmentFinalEndLaneL: number;
  segmentFinalEndLaneR: number;
}

export class ChartRenderer {
  app: Application;
  laneWidth = 250;
  beatSpacing = 50;
  debugRenderUIDs: boolean;

  constructor(debugRenderUIDs = false) {
    this.app = new Application();
    this.debugRenderUIDs = debugRenderUIDs;
  }

  async initialize() {
    await this.app.init({ antialias: true, autoDensity: true });
  }

  public static preProcessChartData(chartData: ChartData): PreProcessedChartFileSoA {
    const { Notes, Bpms, Beats = [] } = chartData;
    const initialBpmValue = Bpms[0]?.Bpm || 120;
    const firstBeatInfo = Beats[0];
    const initialBeatSigTuple: [number, number] | undefined = firstBeatInfo
      ? [firstBeatInfo.Numerator, firstBeatInfo.Denominator]
      : undefined;

    const localSecondsToBeat = (seconds: number): number => {
      if (initialBpmValue === 0) return 0;
      const duration = 60 / initialBpmValue;
      return seconds / duration;
    };

    const holdNotesByTime = new Map<string, Note[]>();
    for (const note of Notes) {
      if (parseFlags(note.Flags).noteType === 'Hold') {
        const notesAtTime = holdNotesByTime.get(note.just) || [];
        notesAtTime.push(note);
        holdNotesByTime.set(note.just, notesAtTime);
      }
    }

    const noteUIDs_acc: number[] = [];
    const noteFlagsNums_acc: number[] = [];
    const noteJustBeats_acc: number[] = [];
    const noteResolvedHolds_acc: number[][] = [];

    const allEventBeats: number[] = [];
    const allChainedNoteUIDs = new Set<number>();

    for (const rawNote of Notes) {
      const currentRawNoteParsedFlags = parseFlags(rawNote.Flags);
      const justBeat = localSecondsToBeat(Number(rawNote.just));
      allEventBeats.push(justBeat);

      const resolvedHolds: number[] = [];
      for (const holdTimeSecStr of rawNote.holds) {
        let holdLinkValue: number;
        let foundLinkUID = -1;

        if (currentRawNoteParsedFlags.noteType === 'Hold') {
          const potentialTargetRawNotes = holdNotesByTime.get(holdTimeSecStr);
          if (potentialTargetRawNotes) {
            for (const targetRawNote of potentialTargetRawNotes) {
              const targetRawNoteParsedFlags = parseFlags(targetRawNote.Flags);
              if (
                currentRawNoteParsedFlags.L2 === targetRawNoteParsedFlags.L1 &&
                currentRawNoteParsedFlags.R2 === targetRawNoteParsedFlags.R1
              ) {
                foundLinkUID = targetRawNote.Uid;
                break;
              }
            }
          }
        }

        if (foundLinkUID !== -1) {
          holdLinkValue = -foundLinkUID; // Negative for UID
          allChainedNoteUIDs.add(foundLinkUID);
          // The linked note's `justBeat` will be added to `allEventBeats` when it's processed.
        } else {
          const holdEndBeat = localSecondsToBeat(Number(holdTimeSecStr));
          holdLinkValue = holdEndBeat; // Positive for beat
          allEventBeats.push(holdEndBeat);
        }
        resolvedHolds.push(holdLinkValue);
      }

      noteUIDs_acc.push(rawNote.Uid);
      noteFlagsNums_acc.push(rawNote.Flags);
      noteJustBeats_acc.push(justBeat);
      noteResolvedHolds_acc.push(resolvedHolds);
    }
    const maxBeat = allEventBeats.length > 0 ? Math.max(0, ...allEventBeats) : 0;

    const rootNoteUIDs: number[] = Notes.filter((note) => !allChainedNoteUIDs.has(note.Uid)).map(
      (note) => note.Uid
    );

    return [
      noteUIDs_acc,
      noteFlagsNums_acc,
      noteJustBeats_acc,
      noteResolvedHolds_acc,
      initialBpmValue,
      initialBeatSigTuple,
      maxBeat,
      rootNoteUIDs
    ];
  }

  private _processChartData(preProcessedDataSoA: PreProcessedChartFileSoA): {
    parsedNotesMap: Map<number, ParsedNote>;
    rootParsedNotes: ParsedNote[];
    maxBeat: number;
    sortedParsedNotes: ParsedNote[];
  } {
    const [
      noteUIDs,
      noteFlagsNums,
      noteJustBeats,
      noteResolvedHoldsArrays,
      _initialBpm,
      _initialBeatSignatureTuple,
      maxBeatData,
      rootNoteUIDsData
    ] = preProcessedDataSoA;

    const parsedNotesMap = new Map<number, ParsedNote>();
    const allParsedNotesList: ParsedNote[] = [];

    for (let i = 0; i < noteUIDs.length; i++) {
      const uid = noteUIDs[i];
      const flagsNum = noteFlagsNums[i];
      const justBeat = noteJustBeats[i];
      const resolvedHoldsFromTuple = noteResolvedHoldsArrays[i];

      const currentFlags = parseFlags(flagsNum);
      const holdsArray: HoldLink[] = resolvedHoldsFromTuple.map((val) => {
        if (val < 0) {
          return { type: 'uid', uid: -val };
        } else {
          return { type: 'beat', beat: val };
        }
      });

      const parsedNote: ParsedNote = {
        uid: uid,
        just: justBeat,
        holds: holdsArray,
        flags: currentFlags
      };
      parsedNotesMap.set(uid, parsedNote);
      allParsedNotesList.push(parsedNote);
    }
    const sortedParsedNotes = [...allParsedNotesList].sort((a, b) => a.just - b.just);

    const rootParsedNotes: ParsedNote[] = [];
    for (const uid of rootNoteUIDsData) {
      const note = parsedNotesMap.get(uid);
      if (note) {
        rootParsedNotes.push(note);
      }
    }

    return {
      parsedNotesMap,
      rootParsedNotes,
      maxBeat: maxBeatData,
      sortedParsedNotes
    };
  }

  renderChart(preProcessedDataSoA: PreProcessedChartFileSoA) {
    this.app.stage.removeChildren();

    const [
      _noteUIDs,
      _noteFlagsNums,
      _noteJustBeats,
      _noteResolvedHoldsArrays,
      _initialBpm,
      initialBeatSignatureTuple,
      _maxBeatFromTuple,
      _rootNoteUIDs
    ] = preProcessedDataSoA;

    const { parsedNotesMap, rootParsedNotes, maxBeat, sortedParsedNotes } =
      this._processChartData(preProcessedDataSoA);
    const firstBeatTimingInfoForGrid = initialBeatSignatureTuple
      ? { N: initialBeatSignatureTuple[0], D: initialBeatSignatureTuple[1] }
      : undefined;
    // Calculate the number of columns needed. +1 because column indexing is 0-based.
    // If maxBeat = 0, numColumns = 1, meaning we prepare for one column area.
    const numColumns = Math.floor(maxBeat / BEATS_PER_COLUMN) + 1;

    const chartAreaWidth = (this.laneWidth + COLUMN_SPACING) * numColumns - COLUMN_SPACING; // Don't include last column's spacing for width
    const fullGridHeightPerColumn = BEATS_PER_COLUMN * (this.beatSpacing + GRID_LINE_THICKNESS);

    this.app.renderer.resize(
      chartAreaWidth + 2 * CHART_PADDING,
      fullGridHeightPerColumn + 2 * CHART_PADDING // Canvas sized for full height columns
    );

    // Draw background only for the grid line areas per column
    const columnBackgrounds = new Graphics();

    for (let i = 0; i < numColumns; i++) {
      const columnXStart = this._getBeatXOffset(i * BEATS_PER_COLUMN);
      let rectYForBg = CHART_PADDING;
      let heightForBg = fullGridHeightPerColumn;

      if (i === numColumns - 1) {
        // If this is the last column
        const firstBeatOfThisColumn = i * BEATS_PER_COLUMN;

        rectYForBg = this._getBeatYOffset(maxBeat);

        const bottomEdgeOfFirstBeatLineInColumn =
          this._getBeatYOffset(firstBeatOfThisColumn) + GRID_LINE_THICKNESS;
        heightForBg = bottomEdgeOfFirstBeatLineInColumn - rectYForBg;

        if (heightForBg < 0) heightForBg = 0; // Ensure non-negative height
      }

      columnBackgrounds.rect(columnXStart, rectYForBg, this.laneWidth, heightForBg);
    }
    columnBackgrounds.fill({ color: CHART_AREA_BACKGROUND_COLOR, alpha: 0.1 });
    this.app.stage.addChild(columnBackgrounds);

    const grid = this._drawGrid(firstBeatTimingInfoForGrid, maxBeat);
    this.app.stage.addChild(grid);

    const notesContainer = new Container();
    rootParsedNotes.forEach((rootNote) => {
      this._drawNoteChain(rootNote, parsedNotesMap, notesContainer);
    });
    this.app.stage.addChild(notesContainer);
    this._drawMeasureComboCounts(
      sortedParsedNotes,
      maxBeat,
      firstBeatTimingInfoForGrid?.N || 4,
      this.app.stage
    );
  }

  private _drawGrid(beatSignature: { N: number; D: number } | undefined, maxBeatValue: number) {
    const gridContainer = new Container();
    const denominator = beatSignature?.D || 4;

    for (let beatIndex = 0; beatIndex <= maxBeatValue; beatIndex++) {
      const yPos = this._getBeatYOffset(beatIndex);
      const barLineGraphics = new Graphics();
      barLineGraphics
        .rect(this._getBeatXOffset(beatIndex), yPos, this.laneWidth, GRID_LINE_THICKNESS)
        .fill(beatIndex % denominator === 0 ? 0xeeeeee : 0x444444);
      gridContainer.addChild(barLineGraphics);
    }
    return gridContainer;
  }

  private _getNormalizedLanePosition(flagValue: number): number {
    return (flagValue / LANE_FLAG_MAX_VALUE) * this.laneWidth;
  }

  private _drawSingleNoteOrHoldHead(note: ParsedNote, container: Container) {
    const noteGridLineTopX = this._getBeatXOffset(note.just);
    const noteGridLineTopY = this._getBeatYOffset(note.just);

    const normL1 = this._getNormalizedLanePosition(note.flags.L1);
    const normR1 = this._getNormalizedLanePosition(note.flags.R1);
    const displayL1 = Math.min(normL1, normR1);
    const displayR1 = Math.max(normL1, normR1);
    const headWidth = displayR1 - displayL1;

    const headGraphics = new Graphics();
    let currentNoteThickness = NOTE_THICKNESS;
    let headColor: number;

    switch (note.flags.noteType) {
      case 'Single':
        headColor = NOTE_COLOR_SINGLE;
        break;
      case 'Trace':
        headColor = NOTE_COLOR_TRACE;
        currentNoteThickness = TRACE_NOTE_THICKNESS;
        break;
      case 'Hold':
        headColor = NOTE_COLOR_HOLD;
        break;
      case 'Flick':
        headColor = NOTE_COLOR_FLICK;
        break;
      default:
        headColor = NOTE_COLOR_DEFAULT;
        break;
    }

    const rectX = noteGridLineTopX + displayL1;
    const yCenteringOffset = (GRID_LINE_THICKNESS - currentNoteThickness) / 2;
    const rectY = noteGridLineTopY + yCenteringOffset;

    headGraphics.roundRect(rectX, rectY, headWidth, currentNoteThickness, NOTE_CORNER_RADIUS);
    headGraphics.fill(headColor);

    if (note.flags.noteType === 'Flick') {
      // Calculate arrow position relative to the drawn note head
      const noteDrawnTopY = rectY;
      const arrowCenterX = noteGridLineTopX + displayL1 + headWidth / 2;
      const arrowBaseY = noteDrawnTopY - 2; // Place arrow base slightly above the note head
      const arrowTipY = arrowBaseY - FLICK_ARROW_SIZE;
      const arrowHalfWidth = FLICK_ARROW_SIZE / 2;

      headGraphics
        .moveTo(arrowCenterX, arrowTipY)
        .lineTo(arrowCenterX - arrowHalfWidth, arrowBaseY)
        .lineTo(arrowCenterX + arrowHalfWidth, arrowBaseY)
        .closePath()
        .fill(FLICK_ARROW_COLOR);
    }
    container.addChild(headGraphics);

    if (this.debugRenderUIDs) {
      const uidStyle = new TextStyle({
        fontFamily: 'Arial',
        fontSize: 10,
        fill: 0xcccccc,
        align: 'center'
      });
      const uidText = new Text({ text: String(note.uid), style: uidStyle });
      const noteDrawnBottomY = rectY + currentNoteThickness;
      uidText.anchor.set(0.5, 0);
      uidText.x = noteGridLineTopX + displayL1 + headWidth / 2;
      uidText.y = noteDrawnBottomY + 2; // Place UID slightly below the note head
      container.addChild(uidText);
    }
  }

  private _drawIndividualHoldSegment(config: HoldSegmentDrawConfig, container: Container) {
    const {
      segmentInitialStartBeat,
      segmentFinalEndBeat,
      segmentInitialStartLaneL,
      segmentInitialStartLaneR,
      segmentFinalEndLaneL,
      segmentFinalEndLaneR
    } = config;

    let currentPieceBeatStart = segmentInitialStartBeat;
    const totalOriginalSegmentDuration = segmentFinalEndBeat - segmentInitialStartBeat;

    while (currentPieceBeatStart < segmentFinalEndBeat) {
      const startColumnIndexOfPiece = Math.floor(currentPieceBeatStart / BEATS_PER_COLUMN);
      const startOfNextColumn = (startColumnIndexOfPiece + 1) * BEATS_PER_COLUMN;

      let pieceRenderEndBeat: number;
      let nextLoopStartBeat: number;

      if (segmentFinalEndBeat >= startOfNextColumn) {
        pieceRenderEndBeat = startOfNextColumn - HOLD_END_COLUMN_ADJUST;
        nextLoopStartBeat = startOfNextColumn;
      } else {
        pieceRenderEndBeat = segmentFinalEndBeat;
        nextLoopStartBeat = segmentFinalEndBeat;
      }

      let pieceStartLaneL, pieceStartLaneR, pieceEndLaneL, pieceEndLaneR;
      if (totalOriginalSegmentDuration <= 0) {
        pieceStartLaneL = segmentInitialStartLaneL;
        pieceStartLaneR = segmentInitialStartLaneR;
        pieceEndLaneL = segmentFinalEndLaneL;
        pieceEndLaneR = segmentFinalEndLaneR;
      } else {
        const startFraction =
          (currentPieceBeatStart - segmentInitialStartBeat) / totalOriginalSegmentDuration;
        const endFraction =
          (pieceRenderEndBeat - segmentInitialStartBeat) / totalOriginalSegmentDuration;

        pieceStartLaneL =
          segmentInitialStartLaneL +
          (segmentFinalEndLaneL - segmentInitialStartLaneL) * startFraction;
        pieceStartLaneR =
          segmentInitialStartLaneR +
          (segmentFinalEndLaneR - segmentInitialStartLaneR) * startFraction;
        pieceEndLaneL =
          segmentInitialStartLaneL +
          (segmentFinalEndLaneL - segmentInitialStartLaneL) * endFraction;
        pieceEndLaneR =
          segmentInitialStartLaneR +
          (segmentFinalEndLaneR - segmentInitialStartLaneR) * endFraction;
      }

      const xPieceStartAbs = this._getBeatXOffset(currentPieceBeatStart);
      const yPieceStartAbs = this._getBeatYOffset(currentPieceBeatStart);
      const xPieceEndAbs = this._getBeatXOffset(pieceRenderEndBeat);
      const yPieceEndAbs = this._getBeatYOffset(pieceRenderEndBeat);

      const displayPieceStartL = Math.min(pieceStartLaneL, pieceStartLaneR);
      const displayPieceStartR = Math.max(pieceStartLaneL, pieceStartLaneR);
      const displayPieceEndL = Math.min(pieceEndLaneL, pieceEndLaneR);
      const displayPieceEndR = Math.max(pieceEndLaneL, pieceEndLaneR);

      const yOffset = GRID_LINE_THICKNESS / 2;
      const pieceGraphics = new Graphics();
      pieceGraphics
        .poly([
          xPieceStartAbs + displayPieceStartL,
          yPieceStartAbs + yOffset,
          xPieceStartAbs + displayPieceStartR,
          yPieceStartAbs + yOffset,
          xPieceEndAbs + displayPieceEndR,
          yPieceEndAbs + yOffset,
          xPieceEndAbs + displayPieceEndL,
          yPieceEndAbs + yOffset
        ])
        .fill({ color: NOTE_COLOR_HOLD, alpha: HOLD_SEGMENT_ALPHA });
      container.addChild(pieceGraphics);

      currentPieceBeatStart = nextLoopStartBeat;
    }
  }

  private _drawHoldSegmentsForNote(
    note: ParsedNote,
    allParsedNotesMap: Map<number, ParsedNote>,
    container: Container
  ): ParsedNote | undefined {
    let currentSegmentStartBeat = note.just;

    const overallInitialLaneL = this._getNormalizedLanePosition(note.flags.L1);
    const overallInitialLaneR = this._getNormalizedLanePosition(note.flags.R1);
    const overallFinalLaneL = this._getNormalizedLanePosition(note.flags.L2);
    const overallFinalLaneR = this._getNormalizedLanePosition(note.flags.R2);

    let currentSegmentStartLaneL = overallInitialLaneL;
    let currentSegmentStartLaneR = overallInitialLaneR;

    let nextNoteInChain: ParsedNote | undefined = undefined;

    const noteHoldStartTime = note.just;
    let noteHoldEndTime = note.just;

    if (note.holds.length > 0) {
      const lastHoldTarget = note.holds[note.holds.length - 1];
      if (lastHoldTarget.type === 'beat') {
        noteHoldEndTime = lastHoldTarget.beat;
      } else {
        const lastLinkedNote = allParsedNotesMap.get(lastHoldTarget.uid);
        if (lastLinkedNote) {
          noteHoldEndTime = lastLinkedNote.just;
        } else {
          noteHoldEndTime = note.just;
        }
      }
    }
    const totalNoteHoldDuration = noteHoldEndTime - noteHoldStartTime;

    for (const holdEnd of note.holds) {
      let segmentEndBeat: number;
      let linkedNote: ParsedNote | undefined = undefined;

      if (holdEnd.type === 'beat') {
        segmentEndBeat = holdEnd.beat;
      } else {
        const chainedNote = allParsedNotesMap.get(holdEnd.uid);
        if (!chainedNote) {
          return undefined;
        }
        segmentEndBeat = chainedNote.just;
        linkedNote = chainedNote;
      }

      let lanesForThisSegmentEndL: number;
      let lanesForThisSegmentEndR: number;

      if (segmentEndBeat <= currentSegmentStartBeat) {
        lanesForThisSegmentEndL = currentSegmentStartLaneL;
        lanesForThisSegmentEndR = currentSegmentStartLaneR;
      } else if (totalNoteHoldDuration < HOLD_END_COLUMN_ADJUST) {
        lanesForThisSegmentEndL = overallFinalLaneL;
        lanesForThisSegmentEndR = overallFinalLaneR;
      } else if (segmentEndBeat <= noteHoldStartTime) {
        lanesForThisSegmentEndL = overallInitialLaneL;
        lanesForThisSegmentEndR = overallInitialLaneR;
      } else if (segmentEndBeat >= noteHoldEndTime) {
        lanesForThisSegmentEndL = overallFinalLaneL;
        lanesForThisSegmentEndR = overallFinalLaneR;
      } else {
        const fraction = (segmentEndBeat - noteHoldStartTime) / totalNoteHoldDuration;
        lanesForThisSegmentEndL =
          overallInitialLaneL + (overallFinalLaneL - overallInitialLaneL) * fraction;
        lanesForThisSegmentEndR =
          overallInitialLaneR + (overallFinalLaneR - overallInitialLaneR) * fraction;
      }

      this._drawIndividualHoldSegment(
        {
          segmentInitialStartBeat: currentSegmentStartBeat,
          segmentFinalEndBeat: segmentEndBeat,
          segmentInitialStartLaneL: currentSegmentStartLaneL,
          segmentInitialStartLaneR: currentSegmentStartLaneR,
          segmentFinalEndLaneL: lanesForThisSegmentEndL,
          segmentFinalEndLaneR: lanesForThisSegmentEndR
        },
        container
      );

      currentSegmentStartBeat = segmentEndBeat;
      currentSegmentStartLaneL = lanesForThisSegmentEndL;
      currentSegmentStartLaneR = lanesForThisSegmentEndR;

      if (linkedNote) {
        if (!nextNoteInChain) {
          nextNoteInChain = linkedNote;
        }
      }
    }
    return nextNoteInChain;
  }
  private _getNoteHeadsToRender(note: ParsedNote, isFirstInChain: boolean): ParsedNote[] {
    const headsToRender: ParsedNote[] = [];

    if (note.flags.noteType !== 'Hold' || isFirstInChain || note.holds.length === 0) {
      headsToRender.push(note);
    }

    if (note.flags.noteType === 'Hold' && note.holds.length > 0) {
      const lastHoldLink = note.holds[note.holds.length - 1];
      if (lastHoldLink.type === 'beat') {
        const endHeadFlags: Flags = {
          noteType: 'Hold',
          L1: note.flags.L2,
          R1: note.flags.R2,
          L2: 0,
          R2: 0
        };
        headsToRender.push({
          uid: note.uid,
          just: lastHoldLink.beat,
          holds: [],
          flags: endHeadFlags
        });
      }
    }
    return headsToRender;
  }

  private _drawMeasureComboCounts(
    sortedNotes: ParsedNote[],
    maxBeat: number,
    numerator: number,
    container: Container
  ) {
    let noteIndex = 0;

    for (
      let measureLineBeat = numerator;
      measureLineBeat <= maxBeat;
      measureLineBeat += numerator
    ) {
      while (
        noteIndex < sortedNotes.length &&
        sortedNotes[noteIndex].just <= measureLineBeat + FLOAT_COMPARISON_EPSILON
      ) {
        noteIndex++;
      }
      if (noteIndex > 0) {
        const comboCountStyle = new TextStyle({
          fontFamily: 'Arial',
          fontSize: 12,
          fill: '0xffffff',
          align: 'left'
        });
        const comboText = new Text({
          text: String(noteIndex),
          style: comboCountStyle
        });

        const columnX = this._getBeatXOffset(measureLineBeat);
        const lineY_gridTop = this._getBeatYOffset(measureLineBeat);
        const lineY_center = lineY_gridTop + GRID_LINE_THICKNESS / 2;

        comboText.anchor.set(0, 0.5);
        comboText.x = columnX + this.laneWidth + COMBO_COUNT_PADDING;
        comboText.y = lineY_center;
        container.addChild(comboText);
      }
    }
  }

  private _drawNoteChain(
    startingNote: ParsedNote,
    allParsedNotesMap: Map<number, ParsedNote>,
    notesContainer: Container
  ) {
    let currentNote: ParsedNote | undefined = startingNote;
    let isFirstInChain = true;

    while (currentNote) {
      const noteToDraw = currentNote;
      let nextLinkedHoldNoteViaUID: ParsedNote | undefined = undefined;

      if (noteToDraw.flags.noteType === 'Hold' && noteToDraw.holds.length > 0) {
        nextLinkedHoldNoteViaUID = this._drawHoldSegmentsForNote(
          noteToDraw,
          allParsedNotesMap,
          notesContainer
        );
      }

      const headsToDraw = this._getNoteHeadsToRender(noteToDraw, isFirstInChain);
      headsToDraw.forEach((headInfo) => {
        this._drawSingleNoteOrHoldHead(headInfo, notesContainer);
      });

      currentNote = nextLinkedHoldNoteViaUID;
      isFirstInChain = false;
    }
  }

  private _getBeatYOffset(beat: number) {
    const beatNo = BEATS_PER_COLUMN - (beat % BEATS_PER_COLUMN);
    const beatHeight = this.beatSpacing + GRID_LINE_THICKNESS;
    return beatNo * beatHeight + CHART_PADDING;
  }

  private _getBeatXOffset(beat: number) {
    return Math.floor(beat / BEATS_PER_COLUMN) * (this.laneWidth + COLUMN_SPACING) + CHART_PADDING;
  }

  get canvas() {
    return this.app.canvas;
  }
}
