import { useEffect, useRef } from 'react';
import { ChartRenderer } from '~/utils/ChartRenderer';
import { Box } from 'styled-system/jsx';
import type { PreProcessedChartFileSoA } from '~/types';

export function ChartViewer({ chart }: { chart: PreProcessedChartFileSoA }) {
  const pixiContainerRef = useRef<HTMLDivElement>(null);
  const rendererRef = useRef<ChartRenderer>(undefined);

  useEffect(() => {
    if (!chart) return;
    const f = async () => {
      rendererRef.current = new ChartRenderer();
      await rendererRef.current.initialize();
      rendererRef.current.renderChart(chart);
      pixiContainerRef.current?.appendChild(rendererRef.current.canvas);
    };
    void f();
    return () => {
      if (!pixiContainerRef.current || !rendererRef.current) return;
      pixiContainerRef.current?.removeChild(rendererRef.current.canvas);
      rendererRef.current?.app.destroy();
    };
  }, [chart]);

  return <Box ref={pixiContainerRef} />;
}
